// Слайдер для дисконта
const swiperDiscount = () => {
  const swiper = new Swiper('.discounts__swiper.swiper.swiper', {

    slidesPerView: 3,
    loop: true,
    speed: 800,
    slidesPerGroup: 3,
    spaceBetween: 30,

    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next.discounts__swiper-btn_next',
      prevEl: '.swiper-button-prev.discounts__swiper-btn_prev',
    },
  });
}

// Слайдер для блока о магазине
const swiperAbout = () => {
  const swiper = new Swiper('.about__swiper.swiper.swiper', {

    slidesPerView: 1,
    loop: false,
    speed: 700,
    spaceBetween: 50,

    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next.about__swiper-btn_next',
      prevEl: '.swiper-button-prev.about__swiper-btn_prev',
    },
  });
}

swiperDiscount()
swiperAbout()